.signup-btn {
    margin-top: 1.5em;
    padding: .7em 2em;
    background: #1877F2;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: .9rem;
}

.logout-btn {
    padding: .7em 2em;
    border: 2px solid #000000;
    cursor: pointer;
    font-size: 1rem;
}


.logout-btn:hover{
    background-color: #000000;
    color: #ffffff;
}