.signup-form {
    box-shadow: 0px 3px 28px 1px rgba(0,0,0,0.2); 
    background: #ffffff;
    width: 400px;
    padding: 2em 1em;
    border-radius: 5px;
    margin-top: 1em;
    display: flex;
    flex-direction: column;
    gap: .7em;
    margin-left: 1em;
}

.signup-form > div {
    display: flex;
    gap: 1em;
}

.signup-form input {
        width: 100%;
        padding: .6em .8em;
        border-radius: 5px;
        outline: none;
        border: 1px solid #aeaeae;
        font-size: .9rem;
}