.main-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3em;
    width: 95%;
    margin-inline: auto;
    padding-top: 2em;
}

.main-container h1 {
    font-size: 3rem;
}

.logged h1 {
    color: #000000;
    font-weight: 400;
}

.login-content {
    text-align: center;
    font-size: 3rem;
    color: rgb(5, 40, 40);
}

@media (min-width: 900px) {
    .main-container {
        flex-direction: row;
        align-items: center;
    }
}